import './modal.less';

import React, {Component} from 'react';

class modal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      icon: props.icon ? props.icon : 'default',
      theme: props.theme ? props.theme : 'blue',
    };

    this.children = props.children ? props.children : [];
  }

  componentWillReceiveProps(props) {
    this.children = props.children ? props.children : [];

    this.setState({
      icon: props.icon ? props.icon : 'default',
      theme: props.theme ? props.theme : 'blue'
    });
  }

  render() {
    return (
      <div className='modal'>
        <div className='bg' />
        <div className='container'>
          <div className={'circle '+this.state.theme} style={{backgroundImage: 'url("/static/assets/modal/'+this.state.icon+'.png")'}}/>
          <div className={'content'}>
            {this.children}
          </div>
        </div>
      </div>
    );
  }
}

export default modal;