window.browser = window.browser ? window.browser : (window.chrome ? window.chrome : {});

import './panel.less';

import Debug      from 'Wdgt/Debug';
import React      from 'react';
import Signal     from 'Wdgt/Signal';
import ReactDOM   from 'react-dom';
import Controller from './Controller';

const LAST_LOCATION = 'Panel.lastLocation';

class Panel {
  constructor() {
    this.init();
  }

  init() {
    Promise.all([
      new Promise((resolve) => {
        if(document.readyState === 'complete') return resolve();
        if(window.addEventListener) window.addEventListener('DOMContentLoaded', resolve);
      })
    ]).then(() => {
      let init = () => {
        window.sdk = browser.extension.getBackgroundPage().sdk;
        if(!sdk) return setTimeout(() => {init()}, 500);

        window.locales = {};
        window.storage = {};
        new Debug();

        window.panel = this;
        window.controller = ReactDOM.render(<Controller />, document.getElementById('page'));
        this.start();
      };

      init();
    });
  }

  start() {
    if(!sdk.loading || !sdk.loading.loaded) return setTimeout(() => {this.start()}, 500);

    window.signals = {
      onStatusChanged: new Signal()
    };

    this.showScreen();
  }

  showScreen() {
    let screen         = 'main',
        status         = sdk.proxy.status,
        lastLocation   = sdk.storage.get(LAST_LOCATION),
        globalStatus   = sdk.proxy.getGlobalStatus();

    storage.status   = status.status;
    storage.location = status.status==='connected' ? status.location : (lastLocation ? lastLocation : 'optimal');

    if(globalStatus === 'unavailable') screen = 'unavailable';

    controller.setState({screen: screen});
  }
}

export default Panel;