import './Menu.less';

import React, {Component} from 'react';

import Modal from 'components/Modal';
import Input from 'components/Input';

const LAST_LOCATION = 'Panel.lastLocation';

class Menu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modal: {
        signIn: false,
        check: false,
        verifyEmail: false,
        signOut: false,
        limit: false
      },
      form: {
        email: false
      },
      disabled: false,
      message: ''
    };

    this.form = {
      email: ''
    };
  }

  toggleModal(modal, form) {
    this.form = {
      email: ''
    };

    if(form) this.form = form;

    let state = this.state;
    state.message = '';
    state.form = {
      email: false
    };
    state.disabled = false;

    let newModal = !state.modal[modal];
    for(let k in state.modal) {
      state.modal[k] = false;
    }
    state.modal[modal] = newModal;
    this.setState(state);
  }

  fieldChanged(field, value) {
    this.form[field] = value;
  }

  signOut() {
    let state = this.state;
    state.message = '';
    state.disabled = true;

    this.setState(state, () => {
      sdk.user.signOut().then(() => {
        sdk.proxy.disconnect().then(() => {
          sdk.storage.set(LAST_LOCATION, 'optimal');
          storage.location = 'optimal';
          this.toggleModal('signOut');
        });
      });
    });
  }

  signIn() {
    let state = this.state;
    state.message = '';
    state.form = {
      email: false
    };

    if(this.form.email.trim() === '') {
      state.message = 'Fill in the highlighted fields.';
      state.form.email = true;
      return this.setState(state);
    }

    if(!this.form.email.trim().match(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) {
      state.message = 'Enter valid email.';
      state.form.email = true;
      return this.setState(state);
    }

    state.disabled = true;

    this.setState(state, () => {
      sdk.user.bnLink(this.form.email).then((response) => {
        let state = this.state;

        if(response && !response.ResponseStatus.success) {
          state.disabled = false;
          state.message = response.ResponseStatus.error_message;
          return this.setState(state);
        }

        if(response && response.link_status === 423) {
          return this.toggleModal('limit');
        }

        this.toggleModal('check');
      });
    });
  }

  render() {
    let link = 'mailto:support@betternet.co?Subject=%5BBetternet%5D%5BchromeExtension%5D&body=';
    link += encodeURIComponent(`






--- Dont Edit Anything Below ---
App version: **`+sdk.config.application.version+`**
OS:[`+(navigator.userAgent.match(/\(([^);]+)/i) ? navigator.userAgent.match(/\(([^);]+)/i)[1] : 'undefined')+`]
User: [`+sdk.user.hash+`][`+(navigator.userAgent.match(/Chrom(e|ium)/g) ? navigator.userAgent.match(/Chrom(e|ium)\/([0-9.]+)/g)[0] : 'undefined')+`]
-----------------------------`);

    return (
      <div id="screenMenu">
        <div className={'menuFace'}>
          <div className={'circle'} />
          {
            !sdk.user.status.elite &&
            <div className={'face'}/>
          }
          {
            sdk.user.status.elite &&
            <div className={'premiumFace'}/>
          }
        </div>
        <div className='back' onClick={() => {controller.setState({screen: 'main'})}}>&#x2190;</div>
        {
          sdk.user.status.elite &&
          <div className={'yourPremium'}><img src='/static/assets/elements/crownGray.png' /><span>Youre Premium!</span></div>
        }
        {
          !sdk.user.status.elite &&
          <div className={'goPremium'} onClick={() => sdk.user.openPopUp('main_promo')}><img src='/static/assets/elements/crownWhite.png' /><span>Go Premium</span></div>
        }
        {
          (!sdk.user.status.bnLink&&!sdk.user.status.authorized) &&
          <div className={'imPremium'} onClick={this.toggleModal.bind(this, 'signIn')}>Im already premium</div>
        }
        {
          (sdk.user.status.bnLink&&!sdk.user.status.authorized) &&
          <div className={'checkEmailVerification'} onClick={this.toggleModal.bind(this, 'verifyEmail', {email: sdk.user.status.login})}>Check Your Email for Verification</div>
        }
        {
          (sdk.user.status.authorized) &&
          <div className={'signOut'} onClick={this.toggleModal.bind(this, 'signOut')}>Sign Out ({sdk.user.status.login})</div>
        }
        <div className="linkButton" onClick={sdk.tabs.openPage.bind(this, link)}>Contact Us</div>
        <div className="linkButton" onClick={sdk.tabs.openPage.bind(this, 'https://support.betternet.co/hc/en-us/categories/201969666-Extensions')}>FAQ</div>
        <div className="linkButton" onClick={sdk.tabs.openPage.bind(this, 'https://www.betternet.co/privacy-policy.html?utm_source=chrome-extension&utm_medium=menu&utm_campaign=privacy')}>Privacy Policy</div>
        <div className="linkButton" onClick={sdk.tabs.openPage.bind(this, 'https://www.betternet.co/transparency.html?utm_source=chrome-extension&utm_medium=menu&utm_campaign=transparency')}>Why Betternet is Free</div>
        {
          this.state.modal.signIn &&
          <Modal
            theme={'blue'}
            icon={'default'}
          >
            <div className={'title'}>Sign In to Your Premium Account</div>
            {this.state.message !== '' && <div className={'message'}>{this.state.message}</div>}
            <Input
              value={this.form.email}
              label='Enter Your Email Here'
              notValid={this.state.form.email}
              disabled={this.state.disabled}
              onChange={this.fieldChanged.bind(this, 'email')}
              onEnter={this.signIn.bind(this)}
            />
            <div className='description'>
              To sign in into your account, please enter your email and password.
            </div>
            <div className={'buttons'}>
              {!this.state.disabled && <div className={'button gray'} style={{width: '90px', marginRight: '10px'}} onClick={this.toggleModal.bind(this, 'signIn')}>Cancel</div>}
              {!this.state.disabled && <div className={'button blue'} style={{width: '90px'}} onClick={this.signIn.bind(this)}>Sign In</div>}
              {this.state.disabled && <div className={'button gray'} style={{width: '90px'}}>Please wait...</div>}
            </div>
          </Modal>
        }
        {
          this.state.modal.verifyEmail &&
          <Modal
            theme={'blue'}
            icon={'default'}
          >
            <div className={'title'}>Email is Not Verified Yet</div>
            {this.state.message !== '' && <div className={'message'}>{this.state.message}</div>}
            <Input
              value={this.form.email}
              label='Enter Your Email Here'
              notValid={this.state.form.email}
              disabled={this.state.disabled}
              onChange={this.fieldChanged.bind(this, 'email')}
              onEnter={this.signIn.bind(this)}
            />
            <div className='description'>
              Check your Inbox or Bulk/Spam  folder to verify your Email address
            </div>
            <div className={'buttons'}>
              {!this.state.disabled && <div className={'button gray'} style={{width: '90px', marginRight: '10px'}} onClick={this.toggleModal.bind(this, 'verifyEmail')}>Cancel</div>}
              {!this.state.disabled && <div className={'button blue'} style={{width: '90px'}} onClick={this.signIn.bind(this)}>Resend</div>}
              {this.state.disabled && <div className={'button gray'} style={{width: '90px'}}>Please wait...</div>}
            </div>
          </Modal>
        }
        {
          this.state.modal.check &&
          <Modal
            theme={'green'}
            icon={'face'}
          >
            <div className='description'>
              Check your Inbox or Bulk/Spam  folder and click on 'Confirm' to verify your Email address.
            </div>
            <div className={'buttons'}>
              <div className={'button green'} style={{width: '90px'}} onClick={this.toggleModal.bind(this, 'check')}>Ok</div>
            </div>
          </Modal>
        }
        {
          this.state.modal.signOut &&
          <Modal
            theme={'red'}
            icon={'error'}
          >
            <div className={'title'}>Signing Out?</div>
            <div className='description'>
              This will disconnect this device from your account <span>{sdk.user.status.login}</span>
            </div>
            <div className={'buttons'}>
              {!this.state.disabled && <div className={'button gray'} style={{width: '90px', marginRight: '10px'}} onClick={this.toggleModal.bind(this, 'signOut')}>Cancel</div>}
              {!this.state.disabled && <div className={'button red'} style={{width: '90px'}} onClick={this.signOut.bind(this)}>Sign Out</div>}
              {this.state.disabled && <div className={'button gray'} style={{width: '90px'}}>Please wait...</div>}
            </div>
          </Modal>
        }
        {
          this.state.modal.limit &&
          <Modal
            theme={'red'}
            icon={'notice'}
          >
            <div className='description'>
              You have reached your limit in adding a new device to your account
            </div>
            <div className={'buttons'}>
              {!this.state.disabled && <div className={'button red'} style={{width: '90px', marginRight: '10px'}} onClick={this.toggleModal.bind(this, 'limit')}>OK</div>}
            </div>
          </Modal>
        }
      </div>
    );
  }
}

export default Menu;
