import React, {Component} from 'react';
import countryData from 'country-data';

class Location extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className='locationContainer'>
        <div
          className={'globe'+(storage.location==='optimal'?' optimal':'')}
          style={(storage.location==='optimal'?{}:{backgroundImage: 'url("/static/assets/flags/'+storage.location+'.svg")'})}
          onClick={() => {controller.setState({screen: 'locations'})}}
        />
        {storage.location==='optimal' && <span onClick={() => {controller.setState({screen: 'locations'})}}>Select Virtual Location</span>}
        {storage.location!=='optimal' && <span onClick={() => {controller.setState({screen: 'locations'})}}>{countryData.countries[storage.location.toUpperCase()].name}</span>}
      </div>
    );
  }
}

export default Location;