import './face.less';
import React, {Component} from 'react';

class face extends Component {
  constructor(props) {
    super(props);

    this.state = {
      face: props.face ? props.face : 1,
      theme: props.theme ? props.theme : 'gray',
      animated: props.animated ? props.animated : false
    }
  }

  componentWillReceiveProps(props) {
    this.setState({
      face: props.face ? props.face : 1,
      theme: props.theme ? props.theme : 'gray',
      animated: props.animated ? props.animated : false
    });
  }

  render() {
    return (
      <div className={'faceContainer ' + this.state.theme + (this.state.animated?' animated':'')}>
        <div className='circle circle1' />
        <div className='circle circle2' />
        <div className='circle circle3' />
        <div className={'face face'+this.state.face.toString()} />
      </div>
    );
  }
}

export default face;