import './Unavailable.less';
import React, {Component} from 'react';

class Unavailable extends Component {
  constructor() {
    super();
  }

  click() {
    sdk.reporting.report('click', 'unavailable');

    sdk.proxy.disableOtherProxy().then(() => {
      return panel.showScreen();
    });
  }

  render() {
    return (
      <div id="screenUnavailable">
        <div className="title">Service interuption</div>
        <div className="description">Betternet is being interrupted by another Chrome extension that is controlling your proxy settings.</div>
        <div className={"button blueButton"} onClick={this.click.bind(this)}>Click here to disable other extension.</div>
      </div>
    );
  }
}

export default Unavailable;