import './Locations.less';

import React, {Component} from 'react';
import countryData from 'country-data';

const LAST_LOCATION = 'Panel.lastLocation';

class Locations extends Component {
  constructor(props) {
    super(props);
  }

  changeLocation(location) {
    /*if(!sdk.user.status.elite) {
      return sdk.user.openPopUp('location_'+location);
    }*/

    if(location !== storage.location) {
      sdk.storage.set(LAST_LOCATION, location);
    }

    storage.location = location;

    if(sdk.proxy.status.status === 'connected') {
      sdk.reporting.report('connection', 'reconnect');
      sdk.proxy.connect(location, true).then(() => {
        controller.setState({screen: 'main'});
      });
    }
    else {
      controller.setState({screen: 'main'});
    }
  }

  render() {
    let locations = [];

    let list = sdk.proxy.getLocations((sdk.user.status.elite?'elite':undefined));
    for(let k in list) {
      locations.push(
        <div className='location' key={k} onClick={this.changeLocation.bind(this, list[k])}>
          <div className={'globe'} style={{backgroundImage: 'url("/static/assets/flags/'+list[k]+'.svg")'}}/>
          <span>{countryData.countries[list[k].toUpperCase()].name}</span>
        </div>
      );
    }

    return (
      <div id={'screenLocations'}>
        <div className='back' onClick={() => {controller.setState({screen: 'main'})}}>&#x2190;</div>
        <div className='header'>
          <div className={'globe'+(storage.location==='optimal'?' optimal':'')}
            style={(storage.location==='optimal'?{}:{backgroundImage: 'url("/static/assets/flags/'+storage.location+'.svg")'})}
          />
          {storage.location==='optimal' && <div className='text'>Select Virtual Location</div>}
          {storage.location!=='optimal' && <div className='text'>{countryData.countries[storage.location.toUpperCase()].name}</div>}
        </div>
        <div className='locationsContainer'>
          {locations}
        </div>
      </div>
    );
  }
}

export default Locations;