import './Promo.less';

import React, {Component} from 'react';

const RATED = 'Panel.promo.rated';
const NEXT_SHOW = 'Panel.promo.nextShow';

class Promo extends Component {
  constructor(props) {
    super(props);

    this.state = {
      promo: this.getPromo(),
      status: props.status
    };
  }

  componentWillReceiveProps(props) {
    this.setState({status: props.status, promo: this.getPromo()});
  }

  click(raiting) {
    if(raiting > 3) {
      sdk.storage.set(RATED, true);
      return sdk.tabs.openPage('https://chrome.google.com/webstore/detail/betternet-unlimited-free/gjknjjomckknofjidppipffbpoekiipm/reviews?hl=en-US');
    }

    return this.setState({promo: (<div className='rateUs'><div className='rateUsTitle'>Thank you!</div></div>)});
  }

  getPromo() {
    if(sdk.proxy.status.status === 'disconnected') {
      return (
        <div className="gdpr" style={{textAlign: 'center'}}>
          By using this extension, you agree to our<br />
          <a href="https://www.betternet.co/terms" target="_blank">Terms of service</a> and <a href="https://www.betternet.co/privacy-policy" target="_blank">Privacy policy</a>.
        </div>
      );
    }

    if(sdk.proxy.status.status !== 'connected' || sdk.storage.get(RATED) || (sdk.storage.get(NEXT_SHOW) && sdk.storage.get(NEXT_SHOW) > Date.now() ) ) {
      return (
        <div className='facebook'>
          <div className='facebookTitle'>Visit us on Facebook</div>
          <div className='facebookButton' onClick={sdk.tabs.openPage.bind(this, 'https://www.facebook.com/betternet.co/')}>
            <div className='facebookLogo' />
            Visit
          </div>
        </div>);
    }

    sdk.storage.set(NEXT_SHOW, (Date.now() + 6 * 60 * 60 * 1000));

    return (
      <div className='rateUs'>
        <div className='rateUsTitle'>Rate us 5 stars on Chrome Web Store</div>
        <div className="stars">
          {[1, 2, 3, 4, 5].reverse().map((rating) => {
            return <div className="star" key={rating} onClick={this.click.bind(this, rating)} />
          })}
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className='promoContainer'>
        {this.state.promo}
      </div>
    );
  }
}

export default Promo;